/* Header.css */
header {
  font-family: 'Oswald', sans-serif;
  background-color: #20232a;
  color: white;
  text-align: center;
  padding: 1em;
}

header h1 {
  margin: 0;
  font-size: 2em;
}

nav ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap; /* 추가: 넘치는 항목을 다음 줄로 넘깁니다 */
}

/* 미디어 쿼리 추가: 화면이 768px 이하일 때 */
@media (max-width: 768px) {
  nav ul {
    flex-direction: column;
    align-items: center;
  }
}

nav ul li {
  padding: 0.5em;
  flex: 1; /* 추가: 각 항목이 유연하게 크기를 조절합니다 */
}

nav ul li a {
  color: white;
  text-decoration: none;
}

nav ul li a:hover {
  border-bottom: 2px solid white;
}
